/*
 * @Author: xiaoshui.candy 
 * @Date: 2019-06-28 10:20:44 
 * @Last Modified by: xiaoshui.candy
 * @Last Modified time: 2019-07-23 10:38:41
 * @desc:'上传文件组件'
 * @params:{Array} [fileType] 文件类型 
 * @params:{Number} [limitNum] 限制上传个数
 * @params:{Array} [files] 文件集合
 * @emitEvent:change 上传文件发生改变事件
 * @example: <fileUpload :fileType="['.apk']" @change="fileChange" :files="fileList"></fileUpload>
 */
<template>
  <div class="upload-box">
    <el-upload
      class="upload"
      :action="uploadUrl"
      :on-success="handleSuccess"
      :before-upload="beforeUpload"
      :before-remove="beforeRemove"
      :on-remove="handleRemove"
      :on-error="handleError"
      :on-exceed="handleExceed"
      :limit="limitNum"
      :file-list="fileList">
      <el-button  class="upload-btn" v-if="fileList.length<limitNum">点击选择文件</el-button>
    </el-upload>
  </div>
</template>

<script>
export default {
  name:'FileUpload',
  props: {
    fileType:{
      type:Array,
      default:()=>[]
    },//文件类型
    limitNum:{
      type:Number,
      default:1
    }, //限制上传个数
    files:{
      type:Array,
      default:()=>[]
    },//文件集合
  },
  data() {
    return {
      fileList:[], //多文件
      uploadUrl:process.env.VUE_APP_UPLOAD +'/common/file/uploadFile'
    };
  },
  watch: {
    'files' (n) {
      this.fileList = n
    }
  },
  created() {
  },
  methods: {
    handleError(err, file, fileList){
      console.log(err,file,fileList)
    },
    //文件上传成功
    handleSuccess(res, file) {
      let obj = {}
      obj.name = res.data.fileName
      obj.url = process.env.VUE_APP_UPLOAD + res.data.filePath
      obj.fileSize = file.size
      obj.fileFormat = res.data.suffixType
      obj.md5 = res.data.md5
      this.fileList.push(obj)
      this.$emit("change",this.fileList)
    },
    //上传文件之前
    beforeUpload(file) {
      let _this = this
      var index = file.name.lastIndexOf('.')
      var suffix = file.name.substring(index, file.name.length)
      let metched  = this.fileType.find(el => el === suffix)
      if (!metched) {
        _this.$message.error('请选择后缀为 ' +this.fileType.join(',') +' 类型的文件上传')
      }
      const isLt100M = file.size / 1024 / 1024 < 100;
      if (!isLt100M) {
        _this.$message.error('文件大小不能超过100MB!');
      }
      return (metched && isLt100M)?true:false;
    },
    //删除文件
    handleRemove(file, fileList) {
      this.fileList = fileList
      this.$emit("change",this.fileList)
    },
    //删除文件之前
    beforeRemove(file, fileList) {
      if (file && file.status==="success") {
        return this.$confirm(`确定移除 ${ file.name }？`,{
          confirmButtonText: '确 认',
          cancelButtonText: '取 消',
          customClass:'infoMessage',
          center:true
        });
      }
    },
    //文件超出个数限制时
    handleExceed(files, fileList) {
      this.$message.warning(`最多只能选择${this.limitNum} 个文件`);
    }
  }
};
</script>

<style lang="less">
.upload-box{
  .upload{
    display:flex;
    flex-wrap: wrap;
    flex-direction: column-reverse;
    .el-button{
      width:100%;
      color: #ffc14f;
      border:1px solid #ffecca;
      background-color: #fff9ed;
    }
    .el-upload-list{
      width:100%;
      .el-upload-list__item-name{
        width:80%;
      }
      .el-icon-document::before{
        content:"\E737";
      }
    }
  }
}
</style>
