import 'babel-polyfill'
import promise from 'es6-promise';
promise.polyfill();
import Vue from 'vue'
import App from './App.vue'
import store from './store'
import mixins from './mixins'
import initRouter from './router'
import VueRouter from 'vue-router'
import axios from './util/axios'
import ElementUI from 'element-ui';
import { sync } from 'vuex-router-sync'
import utils from './util/utils'
import 'element-ui/lib/theme-chalk/index.css';
import './components'
import './assets/icons'
import VueQuillEditor from "vue-quill-editor";
import "quill/dist/quill.core.css";
import "quill/dist/quill.snow.css";
import "quill/dist/quill.bubble.css";

Vue.config.devtools = true
Vue.config.productionTip = false
import VueClipboard from 'vue-clipboard2'
Vue.use(VueClipboard)
Vue.use(VueQuillEditor)
let bus = new Vue()
Vue.prototype.$http = axios
Vue.prototype.$bus = bus
Vue.prototype.$utils = utils
Vue.use(ElementUI);
Vue.use(VueRouter)
Vue.mixin(mixins)
window.document.addEventListener('DOMContentLoaded', async() => {
    let token = localStorage.getItem("token")
    if (token) {
        await store.dispatch('getUserInfo')
    }
    const router = new VueRouter({
        mode: 'hash',
        routes: initRouter()
    })
    let menu = store.state.user.menus&&store.state.user.menus.length>0
    router.beforeEach((to, from, next) => {
        /**
         * 1.如果有token,不是登陆路径,放行
         * 2.如果没有token,不是登陆页面,跳转到登陆
         * 3.如果没有token,去登陆页面的,跳转到登陆页面
         */
        let toLogin = to.path === "/login"
        let other = to.path === "/forget-pass"
        if (menu && toLogin) {
          if (!other) {
            next('/')
          }
          next()
        } else if (menu && !toLogin) {
          next()
        } else if (!menu && !toLogin) {
          if (other) {
            next()
          } else {
            next('/login')
          }
        } else if (!menu && toLogin) {
          next()
        }
    })
    sync(store, router)
    window.app = new Vue({
        router,
        store,
        render: h => h(App),
    }).$mount('#app')
}, false)