const formRule = {
  //大于0可保留两位小数
  validateNumber(rule, value, callback) {
    var reg = /^\d+(\.\d{1,2})?$/
    if ((value && !reg.test(value)) || value === 0) {
      return callback(new Error('请输入大于0的数字(可保留两位小数)'))
    } else {
      return callback()
    }
  },
  //0-100的数字(可保留两位小数)
  validatePercent(rule, value, callback) {
    let reg = /^(\d|[1-9]\d)(\.\d{1,2})?$/
    if (value && !reg.test(value)) {
      if (Number(value) === 100) {
        return callback()
      } else {
        return callback(new Error('请输入0-100的数字(可保留两位小数)'))
      }
    } else {
      return callback()
    }
  },
  //大于0的整数
  validateInteger(rule, value, callback) {
    let reg = /^\+?[1-9]\d*$/
    if (value && !reg.test(Number(value))) {
      return callback(new Error('请输入大于0的整数'))
    } else {
      return callback()
    }
  },
  //效验http或https
  validateLink(rule, value, callback) {
    let reg = /(http|https):\/\/([\w.]+\/?)\S*/
    if (value && !reg.test(value)) {
      return callback(new Error('请输入正确的链接地址'))
    } else {
      return callback()
    }
  },
  //效验不能输入中文
  validateNoChinese(rule, value, callback) {
    let reg = /[\u4E00-\u9FA5]/g
    if (value && reg.test(value)) {
      return callback(new Error('不能输入中文'))
    } else {
      return callback()
    }
  },
  //只能输入英文或数字
  validateEAN(rule, value, callback) {
    let reg = /^[A-Za-z0-9]+$/
    if (value && !reg.test(value)) {
      return callback(new Error('只能输入英文或数字'))
    } else {
      return callback()
    }
  },
  //验证身份证号
  validateIdcard(rule, value, callback) {
    let reg = /(^[1-9]\d{5}(18|19|([23]\d))\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\d{3}[0-9Xx]$)|(^[1-9]\d{5}\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\d{3}$)/;
    if (value && !reg.test(value)) {
      return callback(new Error('请输入正确的身份证号'))
    } else {
      return callback()
    }
  },
  //验证密码（8~16位，至少包含数字/字母/字符两种组合）
  validatePassword(rule, value, callback) {
    let reg = /^(?=.*[A-Za-z])(?=.*\d)[A-Za-z\d]{8,16}$/
    if (value && !reg.test(value)) {
      return callback(new Error('8~16位，至少包含数字/字母两种组合'))
    } else {
      return callback()
    }
  },
  // 验证手机号
  validateMobile(rule, value, callback) {
    let mobile = /^1[3|4|5|6|7|8|9][0-9]{9}$/g
    if (value && !mobile.test(value)) {
      return callback(new Error('手机号格式错误'))
    } else {
      return callback()
    }
  },
  // 验证手机号及固定电话
  validatePhone(rule, value, callback) {
    let mobile = /^1[3|4|5|6|7|8|9][0-9]{9}$/g
    let phone = /^((\d{3,4})-)(\d{6,8})(-(\d{3,}))?$/
    if (value && (!(mobile.test(value) || phone.test(value)))) {
      return callback(new Error('请输入正确的手机号或固话'))
    } else {
      return callback()
    }
  },
  // 验证邮箱
  validateEmail(rule, value, callback) {
    let email = /^(\w)+(\.\w+)*@(\w)+((\.\w+)+)$/
    if (value) {
      if (email.test(value)) {
        return callback()
      } else {
        return callback(new Error('邮箱格式错误'))
      }
    } else {
      return callback()
    }
  },
  // 验证版本号
  validateVersionNo(rule, value, callback) {
    let reg = /^(\w{1})(\w|\.|-|\s*)+$/
    if (value !== undefined) {
      if (reg.test(value)) {
        return callback()
      } else {
        return callback(new Error('版本号格式错误'))
      }
    }
  }
}

export default formRule