import axios from '../util/axios'
var user = {
  state: {
    userInfo: null,
    menus:[]
  },
  mutations: {
    //获取用户jwt
    UPDATE_USERINFO(state,data) {
      state.userInfo = data
      localStorage.setItem('token',data.token)
      localStorage.setItem('realName',data.realName)
      localStorage.setItem('accountNo',data.accountNo)
      localStorage.setItem('organizationName',data.organizationName)
    },
    //更新菜单
    UPDATE_MENUS(state,data){
      let resultMenuList = JSON.parse(JSON.stringify(data.menuList).replace(/link/g, 'path'))
      resultMenuList.forEach(item=>{
        if(item.children&&item.children.length>0){
          item.path = item.children[0].path
          //子模块排序
          item.children.sort((next, pre) =>{
            !pre.index?pre.index=0:''
            !next.index?next.index=0:''
            return (+next.index) - (+pre.index)
          })
          item.children.forEach(it=>{
            if(it.children&&it.children.length===0){
              delete it.children
            }
          })
        }else{
          delete item.children
        }
      })
      //排序
      resultMenuList.sort((next, pre) =>{
        !pre.index?pre.index=0:''
        !next.index?next.index=0:''
        return (+next.index) - (+pre.index)
      })
      state.menus = resultMenuList
    }
  },
  actions: {
    //登出
    logOut() {
      var _resolve,_reject
      let promise = new Promise((resolve,reject)=>{
        _resolve = resolve
        _reject = reject
      })
      axios.post(process.env.VUE_APP_API + '/login/loginOut').then((res) => {
        _resolve(res)
        localStorage.removeItem("token")
        localStorage.removeItem("realName")
        localStorage.removeItem("accountNo")
        localStorage.removeItem("organizationName")
        window.location.reload()
      }).catch(e=>{
        _reject(e)
      })
      return promise
    },
    //登录
    login(store,user) {
      var _resolve,_reject
      let promise = new Promise((resolve,reject)=>{
        _resolve = resolve
        _reject = reject
      })
      axios.noTokenPost(process.env.VUE_APP_API + '/login/loginByPwd', user).then((res) => {
        if(res.code===200){
          store.commit('UPDATE_USERINFO', res.data)
        }
        _resolve(res)
      }).catch((err)=>{
        _reject(err)
      })
      return promise
    },
    /**
     * 
     * @param {*} store 
     * @param {*} args 
     * 如果state里面有数据了,就从state拿,否则从接口拿
     */
    async getUserInfo(store, args) {
      let res = await axios.post(process.env.VUE_APP_API + '/role/getLoginUserMenuList',{
        projectType:2
      })
      if(res.code===200){
        store.commit('UPDATE_MENUS', res.data) //更新菜单
      } 
    }
  }
}
export default user

