/*
 * @Author: xiaoshui.candy 
 * @Date: 2019-06-05 13:28:46 
 * @Last Modified by: xiaoshui.candy
 * @Last Modified time: 2019-07-23 10:36:35
 * @desc:'地址组件'
 * @params:{Object} [address] 地址对象 
 * @params:{Number} [level] 级别 (3:省市区,4省市区县,5默认完整地址)
 * @emitEvent:getAddress 地址改变事件
 * @example: <Address :address='addressObj' :level="3" @getAddress='getReportPlace' ref="addressEvent"></Address> 
 */
<template>
  <div class="area">
    <div class="el-form-item-firstchild">
      <div class="selectbox">
        <el-select v-model="value" @change="getCity(true)" class="set-el-select-item" placeholder="请选择省">
          <el-option
            v-for="item in getProvinceList"
            :key="item.provinceId"
            :label="item.provinceName"
            :value="item.provinceId">
          </el-option>
        </el-select>
        <el-select v-model="value2" placeholder="请选择市" @change="getCounty(true)" class="set-el-select-item">
          <el-option
            v-for="item in getCityList"
            :key="item.cityId"
            :label="item.cityName"
            :value="item.cityId">
          </el-option>
        </el-select>
        <el-select v-model="value3" placeholder="请选择区/县" @change="getTown(true)"  class="set-el-select-item">
          <el-option
            v-for="item in getCountyList"
            :key="item.countyId"
            :label="item.countyName"
            :value="item.countyId">
          </el-option>
        </el-select>
        <el-select v-model="value4" placeholder="请选择乡/镇/街道" @change="getVillage(true)" class="set-el-select-item" :disable="isDisable" v-if="level>3">
          <el-option
            v-for="item in getTownList"
            :key="item.townId"
            :label="item.townName"
            :value="item.townId">
          </el-option>
        </el-select>
        <el-select v-model="value5" ref="test"  :disable="isDisable2" @change="sendvillage($event)" placeholder="请选择村/社区居委会" class="set-el-select-item" v-if="level>4">
          <el-option
            v-for="item in getVillageList"
            :key="item.villageId"
            :label="item.villageName"
            :value="item.villageId">
          </el-option>
        </el-select>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name:'Address',
  props: {
    address: {
      type: Object,
      default:()=>{}
    }, //地址集合
    level:{
      type:Number,
      default:5
    } //级别
  },
  data() {
    return {
      value: '',
      value2: '',
      value3: '',
      value4: '',
      value5: '',
      getProvinceList: [], // 省
      getCityList: [], // 市
      getCountyList: [], // 县 区
      getTownList: [], //镇
      getVillageList: [],
      addressObj: {},
      isDisable: false,
      isDisable2: false
    }
  },
  created () {
    this.getProvince()
  },
  watch: {
    address:{
      handler(val) {
        this.init();
      },
      deep: true
    }
  },
  methods: {
    //初始化
    init(){
      this.getProvince()
      this.value = this.address.provinceId
      this.getCity()
      this.value2 = this.address.cityId
      this.getCounty()
      this.value3 = this.address.countyId
      if(this.address.townId){
        this.getTown()
        this.value4 = this.address.townId
      }else{
        this.value4 = ''
      }
      if(this.address.villageId){
        this.getVillage()
        this.value5 = this.address.villageId
      }else{
        this.value5 = ''
      }
    },
    getProvince () {
      var data = {}
      this.$http.post(process.env.VUE_APP_API + '/dict/getProvince', data).then(res => {
        if (res.code === 200) {
          this.getProvinceList = res.data
        }
      })
    },
    getCity (flag) {
      var data = {
        provinceId: this.value
      }
      this.$http.post(process.env.VUE_APP_API + '/dict/getCity', data).then(res => {
        if(res.code == 200) {
          this.getCityList = res.data
          this.getCityList.forEach((item, index) => {
            this.addressObj.provinceName = item.provinceName
            this.addressObj.provinceId = item.provinceId
          })
          if (flag && this.getCountyList.length > 0) {
            this.value2 = this.getCityList[0].cityId
            console.log('城市ID', this.value3)
            this.getCounty(true)
          }
          this.$emit('getAddress', this.addressObj)
        }
      })
    },
    getCounty (flag) {
      var data = {
        cityId: this.value2
      }
      this.$http.post(process.env.VUE_APP_API + '/dict/getCounty', data).then(res => {
        if(res.code == 200) {
          this.getCountyList = res.data
          this.getCountyList.forEach((item, index) => {
            this.addressObj.cityName = item.cityName
            this.addressObj.cityId = item.cityId
          })
          if (flag && this.getTownList.length > 0&&this.level>3) {
            this.value3 = this.getCountyList[0].countyId
            console.log('县ID', this.value3)
            this.getTown(true)
          }
          this.$emit('getAddress', this.addressObj)
        }
      })
    },
  getTown(flag) {
      var data = {
        countyId: this.value3
      }
      this.$http.post(process.env.VUE_APP_API + '/dict/getTown', data).then(res => {
        if(res.code == 200) {
          this.getTownList = res.data
          if(this.getTownList.length > 0) {
              this.getTownList.forEach((item, index) => {
              this.addressObj.countyName = item.countyName
              this.addressObj.countyId = item.countyId
            })
            if (flag && this.getTownList.length > 0&&this.level>4) {
              this.value4 = this.getTownList[0].townId
              console.log('镇ID', this.value4)
              this.getVillage(true)
            }
            this.$emit('getAddress', this.addressObj)
          } else {
            this.isDisable = true
          }
        }
      })
    },
    getVillage (flag) {
      var data = {
        townId: this.value4
      }
      this.$http.post(process.env.VUE_APP_API + '/dict/getVillage', data).then(res => {
        if(res.code == 200) {
          this.getVillageList = res.data
          if(this.getVillageList.length > 0) {
              this.getVillageList.forEach((item, index) => {
              this.addressObj.townName = item.townName
              this.addressObj.townId = item.townId
            })
            if (flag && this.getVillageList.length > 0) {
              this.value5 = this.getVillageList[0].villageId
              console.log('村ID', this.value5)
            }
            this.$emit('getAddress', this.addressObj)
          }else {
            this.isDisable2 = true
          }
          if (this.value5) {
            this.sendvillage()
          }
        } 
      })
    },
    sendvillage (e) {
      this.addressObj.villageId = this.value5
      this.getVillageList.map((item, index) => {
        if (item.villageId === this.value5) {
          console.log('拿到', item.villageName)
          this.addressObj.villageName = item.villageName
        }
      })
      this.$emit('getAddress', this.addressObj)
    }
  }
}
</script>
<style lang="scss" scoped>
  .el-form-item-firstchild {
    display:table;
    .selectbox {
      display: table-cell;
      vertical-align: top;
    }
    .labeTxt {
      display: table-cell;
      vertical-align: top;
      padding-right:10px;
      white-space: nowrap;
      min-width: 150px;
      max-width: 150px;
      text-align:right;
      .strong {
        color:red;
      }
    }
    .set-el-select-item {
      margin-right: 10px;
    }
  }

</style>

