<template>
  <div class="main">
    <Sider/>
    <section
      class="right-content loading-box"
      v-loading="loading"
      element-loading-text="loading加载中"
      element-loading-background="rgba(0, 0, 0, 0.7)"
    >
      <Header/>
      <div class="viewer">
        <transition name="fade-move">
          <router-view class="viewer-detail" @viewLoad="doLoading"></router-view>
        </transition>
      </div>
    </section>
  </div>
</template>

<script>
import Sider from "@/components/sider";
import Header from "@/components/Header";
export default {
  name: "index",
  components: {
    Sider,
    Header
  },
  data() {
    return {
      loading: false
    };
  },
  methods: {
    doLoading(bour) {
      bour
        ? (this.loading = true)
        : setTimeout(() => {
            this.loading = false;
          }, 400);
    }
  }
};
</script>

<style lang="scss">
@import "../assets/styles/index.scss";
.main {
  font-size: 14px;
  font-family: "微软雅黑", Arial, Helvetica, sans-serif;
  @include flex(row);
  .right-content {
    flex: 1;
    background: #f6f6f5;
    padding:0px 24px;
    @include flex;
    position: relative;
    &::before{
      position:absolute;
      width:100%;
      height:320px;
      content:'';
      left:0;
      background-image: linear-gradient(0deg, #ffe959 0%, #ffbf4f 100%);
    }
  }
  .viewer {
    background:#fff;
    flex: 1;
    overflow: auto;
    overflow-x: hidden;
    position: relative;
    border-radius:10px;
    @include flex;
    &-detail {
      box-sizing: border-box;
      width: 100%;
      position: absolute;
      height:100%;
    }
  }
  .fade-move-enter-active,
  .fade-move-leave-active {
    transition: 0.3s all ease;
  }
  .fade-move-enter,
  .fade-move-leave-to {
    opacity: 0;
    transform: translateX(-100px);
  }
  .fade-move-enter-to,
  .fade-move-leave {
    opacity: 1;
    transform: translateX(0);
  }
}
</style>
