import Vue from 'vue'
import { mapGetters } from 'vuex'
import { Menu, MenuItem, Submenu } from 'element-ui'
import './sider.scss'
import logo from "../../assets/images/logo.png"
Vue.component(Menu.name, Menu)
Vue.component(MenuItem.name, MenuItem)
Vue.component(Submenu.name, Submenu)
export default {
  name: 'sider',
  data () {
    return {
      defaultActive: ''
    }
  },
  computed: {
    ...mapGetters(['getMenuCollapse'])
  },
  watch:{
    $route(n,o){
      if(n){
        this.handleDefaultActive()
      }
    }
  },
  created () {
    this.handleDefaultActive() //菜单栏默认选中
  },
  methods: {
    //菜单栏默认选中
    handleDefaultActive(){
      this.$router.options.routes[0].children.forEach(item=>{
        if(item.path == this.$route.path){
          if(item.hidden===true){
            this.defaultActive = item.parentPath
          }else{
            this.defaultActive = item.path
          }
        }
      })
    },
    handleOpen (key, keyPath) {
      console.log(key, keyPath)
    },
    handleClose (key, keyPath) {
      console.log(key, keyPath)
    },
    renderMenu (menus) {
      return menus.map(menu => {
        if (menu.children) {
          const icon = menu.icon ? <cc-svg-icon icon-class={menu.icon}></cc-svg-icon> : ''
          if(!menu.hidden){
            return (
              <el-submenu index={menu.path}>
                <template slot="title">
                  {icon}
                  <span>{menu.name}</span>
                </template>
                {this.renderMenu(menu.children)}
              </el-submenu>
            )
          }else{
            return (
              <el-menu-item index={menu.path}>
                {icon}
                <span slot="title">{menu.name}</span>
              </el-menu-item>
            )
          }
        } else {
          const icon = menu.icon ? <cc-svg-icon icon-class={menu.icon}></cc-svg-icon> : ''
          return (
            <el-menu-item index={menu.path}>
              {icon}
              <span slot="title">{menu.name}</span>
            </el-menu-item>
          )
        }
      })
    }
  },

  render () {
    let MenuConfig = this.$store.state.user.menus
    return (
      <div class="sider">
        <div class="sider-logo">
         <img src={logo}/>
        </div>
        <el-menu
          default-active={this.defaultActive}
          class="el-menu-vertical-demo"
          router={true}
          unique-opened={true}
          collapse={this.getMenuCollapse}
          open={this.handleOpen}
          close={this.handleClose}>
          {this.renderMenu(MenuConfig)}
        </el-menu>
      </div>)
  }
}
