import axios from 'axios'
import Vue from 'vue'
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css'
Vue.use(ElementUI)
const qs = require('qs');
let $this = new Vue()
axios.interceptors.request.use(config => {
  return config
}, error => {
  return Promise.reject(error)
})

axios.interceptors.response.use(response => {
  return response
}, error => {
  return Promise.resolve(error.response)
})

const checkStatus = response => {
  // console.log(response.status,response.data)
  if (response && (response.status === 200 )) {
    if(response.data.code === 401){
      // 在这里清除一下token
      localStorage.removeItem("token")
      localStorage.removeItem("realName")
      localStorage.removeItem("organizationName")
      $this.$message.error('登录状态已失效，请重新登录')
      setTimeout(() => {
        window.location.reload()
      }, 1000)
    }else{
      return response.data
    }
  }else if (response && (response.status === 400)) {
		$this.$message.error(response.data.msg)
		return
  }else if (response && (response.status === 500)) {
		$this.$message.error('服务器忙请稍后再试')
		return
  }
}

export default {
  noTokenPost(url, data,trans=false) {
    return axios({
      method: 'post',
      url,
      data: trans ? qs.stringify(data) : data,
      timeout: 10000,
      headers: {
        'X-Requested-With': 'XMLHttpRequest'
      }
    }).then(
      (response) => {
        return checkStatus(response)
      }
    ).then(
      (res) => {
        return res
      }
    )
  },
  post(url, data,trans=true) {
    return axios({
      method: 'post',
      url,
      data: trans ? qs.stringify(data) : data,
      timeout: 10000,
      headers: {
        'X-Requested-With': 'XMLHttpRequest',
        'Content-Type': trans?'application/x-www-form-urlencoded':'application/json;charset=UTF-8',
        token:localStorage.getItem('token')
      }
    }).then(
      (response) => {
        return checkStatus(response)
      }
    ).then(
      (res) => {
        return res
      }
    )
  },
  get(url, params,) {
    return axios({
      method: 'get',
      url,
      params, // get 请求时带的参数
      timeout: 10000,
      headers: {
        'X-Requested-With': 'XMLHttpRequest',
        token:localStorage.getItem('token')
      }
    }).then(
      (response) => {
        return checkStatus(response)
      }
    ).then(
      (res) => {
        return res
      }
    )
  },
  put(url, data) {
    return axios({
      timeout: 10000,
      method: 'put',
      url,
      data:data, // put 请求时带的参数
      headers: {
        'X-Requested-With': 'XMLHttpRequest',
        'Content-Type': 'application/json;charset=UTF-8',
        token:localStorage.getItem('token')
      }
    }).then((response) => {
      return checkStatus(response)
    }).then((res) => {
      return res
    })
  },
  delete(url, params) {
    return axios({
      timeout: 10000,
      method: 'delete',
      url,
      params, // delete 请求时带的参数
      headers: {
        'X-Requested-With': 'XMLHttpRequest',
        token:localStorage.getItem('token')
      }
    }).then((response) => {
      return checkStatus(response)
    }).then((res) => {
      return res
    })
  }
}
