 * @Date: 2019-06-13 15:27:18 
 * @Last Modified by: xiaoshui.candy
 * @Last Modified time: 2019-07-23 10:35:26
 * @desc:'面包屑导航'
 * @params:{Boolean} [isCustom] 是否启用自定义三级名称 
 * @params:{Number} [isLevel] 导航等级
 * @example: <Breadcrumb :isLevel='2'></Breadcrumb>  //二级导航
 * @example: <Breadcrumb :isCustom='false'></Breadcrumb> //三级导航
 * @example :<Breadcrumb><span slot="custom">自定义三级名称</span></Breadcrumb> //自定义三级导航
 */
<!--  面包屑导航 -->
<template>
  <div class="breadcrumb-box">
    <template v-if="getBreadcrumbItems.length>0">
      <el-breadcrumb separator-class="el-icon-arrow-right" class="breadCrumb-box">
        <el-breadcrumb-item v-for="(item,index) in getBreadcrumbItems" :key="index" :to="item.path">{{item.name}}</el-breadcrumb-item>
        <el-breadcrumb-item v-if="isLevel>=3"><slot name="custom">{{!isCustom?threeLevelName:''}}</slot></el-breadcrumb-item>
      </el-breadcrumb>
    </template>
  </div>
</template>

<script>
import Vue from 'vue'
import { mapActions, mapGetters } from 'vuex'
import { Breadcrumb, BreadcrumbItem } from 'element-ui'
Vue.component(Breadcrumb.name, Breadcrumb)
Vue.component(BreadcrumbItem.name, BreadcrumbItem)
export default {
  name:'Breadcrumb',
  props:{
    isCustom:{
      type:Boolean,
      default:true
    }, //是否启用默认三级名称
    isLevel:{
      type:Number,
      default:3
    } //导航等级
  },
  data () {
    return {
      threeLevelName:'' //三级页面名称
    }
  },
  computed: {
    ...mapGetters(['getBreadcrumbItems'])
  },
  mounted() {
    this.changeBreadcrumb(this.$route);
  },
  watch: {
    $route() {
      this.changeBreadcrumb(this.$route);
    }
  },
  created () {
  },
  methods: {
    ...mapActions(["BREADCRUMB_ITEMS"]),
    changeBreadcrumb(route) {
      let oneLevelMenu = {};
      let twoLevelMenu = {};
      let MenuConfig = this.$store.state.user.menus
      MenuConfig.forEach(menu => {
        if (menu.children) {
          const item = menu.children.find(item => item.path === route.path);
          if (item) {
            oneLevelMenu = menu;
            twoLevelMenu = item;
            return false;
          } else {
            menu.children.forEach(el => {
              const it = this.$router.options.routes[0].children.find(is =>is.parentPath===el.path&&is.path === route.path);
               if (it) {
                oneLevelMenu = menu;
                twoLevelMenu = el;
                this.threeLevelName = it.title
                return false;
              }
            });
          }
        } else {
          if (menu.path === route.path) {
            oneLevelMenu = menu;
          }else{
            if(Object.keys(twoLevelMenu).length > 0){
              return twoLevelMenu
            }
            //处理一级栏目下的导航
            const it = this.$router.options.routes[0].children.find(is =>is.parentPath===menu.path&&is.path===route.path);
            if (it) {
              oneLevelMenu = menu;
              let obj = {}
              obj.path = it.path
              obj.name = it.title
              twoLevelMenu = obj
              return false;
            }
          }
        }
      });
      if (Object.keys(twoLevelMenu).length > 0) {
        this.BREADCRUMB_ITEMS([oneLevelMenu, twoLevelMenu]);
      } else {
        this.BREADCRUMB_ITEMS([oneLevelMenu]);
      }
    }
  }
}
</script>

<style lang="scss">
 .breadCrumb-box{
   .el-breadcrumb__item{
     font-size:16px;
   }
   .el-breadcrumb__inner.is-link{
     font-size:16px;
     color:#746651;
     font-weight:normal;
     &:hover{
       cursor: pointer;
     }
   }
   .el-breadcrumb__item:last-child{
     .el-breadcrumb__inner{
       color:#318bfe;
     }
   }
 }
</style>
