/*
 * @Author: xiaoshui.candy 
 * @Date: 2019-06-13 15:14:27 
 * @Last Modified by: xiaoshui.candy
 * @Last Modified time: 2019-07-23 10:39:23
 * @desc:'分页组件'
 * @params:{number} [goPage] 当前页 
 * @params:{number} [pageNum] 每页条数
 * @params:{number} [totalNum] 总条数
 * @emitEvent:updateList 分页发生改变事件
 * @example: <Paginaction class='pagination' :goPage="pageNum" :totalNum="totalNum" @updateList="changePage" ref="paginationEvent"/>
 */
<!-- 分页 -->
<template>
  <div class="pagination_box">
    <div class="pagination_relative">
      <div class="pagination_abs">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          background
          :pager-count="5"
          :current-page="currentPage"
          :page-sizes="pageSizeArray"
          :page-size="pageSize"
          layout="total, sizes, prev, pager, next"
          :total="totalPage">
        </el-pagination>
        <div>跳转到<input type="number" v-model="jumpPage" class="input-page">页</div>
        <button class="confirm_btn" @click="handleClickChange">确定</button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name:'Paginaction',
  props: ['goPage', 'totalNum','pageNum'],
  data () {
    return {
      currentPage: 1,
      pageSize: 10,
      jumpPage:1,
      totalPage:0,
      pageSizeArray: []
    }
  },
  created () {
    this.pageSize = this.pageNum?this.pageNum:10
    this.jumpPage = this.goPage
    this.totalPage = this.totalNum
    this.pageSizeArray = []
    for(let i=1; i<=5 ;i++){
      this.pageSizeArray.push(this.pageSize*i)
    }
  },
  watch:{
    goPage(n,o){
      this.jumpPage = this.goPage
    },
    totalNum(n,o){
      this.totalPage = this.totalNum
    },
    pageNum(n,o){
      this.pageSize = this.pageNum
    }
  },
  methods: {
    //页数改变
    handleSizeChange (v) {
      this.pageSize = v
      let maxPage = Math.ceil(this.totalPage / v)
      if (!this.jumpPage || this.jumpPage <= 0 || this.jumpPage > maxPage) {
        return false
      }
      this.currentPage = Number(this.jumpPage)
      this.initPageList()
    },
    //选择某页
    handleCurrentChange (v) {
      this.currentPage = v
      this.jumpPage = v
      this.initPageList()
    },
    //确定按钮
    handleClickChange () {
      let maxPage = Math.ceil(this.totalPage / this.pageSize)
      if (!this.jumpPage || this.jumpPage <= 0 || this.jumpPage > maxPage) {
        return false
      }
      this.currentPage = Number(this.jumpPage)
      this.initPageList()
    },
    initPageList (key) {
      let params = {}
      params.pageNum = this.currentPage
      params.pageSize = this.pageSize
      this.$emit('updateList',params)
    }
  }
}
</script>

<style lang="scss" scoped>
  .pagination_box{
     width: 100%;
  }
  .pagination_relative{
    width: 100%;
    height: 58px;
    position: relative;
  }
  .pagination_abs{
    width: 100%;
    height: 58px;
    position: absolute;
    left: 0px;
    top: 0px;
    display: flex;
    display: -webkit-flex;
    justify-content: center;
    align-items: center;
  }
  .input-page{
    width:30px !important;
  }
  .confirm_btn{
    height: 28px;
    width: 50px;
    background: none;
    border: 0;
    border-radius: 4px;
    border: solid 1px #e5e5e5;
    font-family: MicrosoftYaHei;
    font-size: 14px;
    font-weight: normal;
    font-stretch: normal;
    letter-spacing: 0px;
    color: #466791;
    cursor: pointer;
    margin-left: 8px;
    outline: none;
  }
  input::-webkit-outer-spin-button,input::-webkit-inner-spin-button{
    -webkit-appearance: none !important;
  }
  .input-page{
    width: 50px;
    height: 28px;
    outline: none;
    margin: 0 5px;
    border-radius: 4px;
    border: solid 1px #d4e1fe;
    padding: 0 5px;
    font-family: MicrosoftYaHei;
    font-size: 12px;
    font-weight: normal;
    font-stretch: normal;
    letter-spacing: 0px;
    color: #466791;
    text-align: center;
    line-height: 28px;
  }
</style>
