import allRouters from './components/index'
import store from '../store'
import Index from '@/views/Index.vue'
export default function () {
  /**
   * 本地路由与授权路由的同步信息
   */
  let matchRouters =[]
  let MenuConfig = store.state.user.menus
   MenuConfig.forEach(route => {
    if(route.children&&route.children.length>0){
      route.children.forEach(item=>{
        let routers = allRouters.filter(menu => {
          return item.path === menu.path ||item.path === menu.parentPath
        })
        if(routers){
          matchRouters.push(...routers)
        }
      })
    }else{
      let home = allRouters.filter(it => {
        return route.path === it.path || route.path === it.parentPath
      })
      if(home){
        matchRouters.push(...home)
      }
    }
  })
  //默认路由
  const IndexRoute = {
    path: '/',
    component: Index,
    children: []
  }
  let routes = [
    IndexRoute,
    {
      path: '/login',
      component: () => import('@/views/login/login.vue')
    },
    {
      path: '/forget-pass',
      component: () => import('@/views/login/forget-pass.vue')
    },
    {
      path: '*',
      redirect: '/login'
    }
  ]
  IndexRoute.children = [...IndexRoute.children, ...matchRouters]
  let first = IndexRoute.children.length>0?IndexRoute.children[0].path:'/login'
  IndexRoute.redirect = first
  return routes
}