 * @Author: xiaoshui.candy 
 * @Date: 2019-07-23 10:26:48 
 * @Last Modified by: xiaoshui.candy
 * @Last Modified time: 2019-07-23 10:37:38
 * @desc:'svg图标'
 * @params:{String} [iconClass] svg类名 
 * @params:{String} [className] svg名称
 * @example: <cc-svg-icon class-name="img-box" icon-class="admin"></cc-svg-icon>
 */
<template>
  <svg v-on="$listeners" class="cc-svg-icon" :class="svgClass" aria-hidden="true">
    <use :xlink:href="iconName"></use>
  </svg>
</template>
<script>
export default {
  name: 'cc-svg-icon',
  props: {
    iconClass: {
      type: String
    },  // svg类名
    className: {
      type: String
    } //svg名称
  },
  computed: {
    iconName () {
      return `#icon-${this.iconClass}`
    },
    svgClass () {
      if (this.className) {
        return `svg-icon ${this.className}`
      }
      return `svg-icon`
    }
  }
}
</script>
<style lang="scss" scoped>
.cc-svg-icon {
  width: 16px;
  height: 16px;
  vertical-align: middle;
  fill: currentColor;
  overflow: hidden;
}
</style>
