<!--  公共头部 -->
<template>
  <header class="header">
    <div class="header-left">
      <h1>家庭医生服务平台</h1>
    </div>
    <div class="header-right">
      <el-dropdown>
        <div class="user-box">
          <div class="user-img">
            <cc-svg-icon class-name="img-box" icon-class="admin"></cc-svg-icon>
          </div>
          <div class="user-info">
              <p>{{realName}}</p>
              <p>{{organizationName}}</p>
          </div>
            <div class="el-dropdown-link">
              <i class="el-icon-arrow-down el-icon--right"></i>
            </div>
        </div>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item @click.native="changePassDialog=true">修改密码</el-dropdown-item>
          <el-dropdown-item @click.native="logoutDialog=true">退出登录</el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>

      <!-- 退出登录提示框 -->
      <el-dialog :visible.sync="logoutDialog" width="330px" custom-class="infoDialog">
        <span>确认退出登录？</span>
        <div slot="footer" class="dialog-footer">
          <el-button @click="logoutDialog = false">取 消</el-button>
          <el-button type="primary" @click="handlLogOut">确 认</el-button>
        </div>
      </el-dialog>
      <!-- 修改密码弹窗 -->
      <el-dialog title="修改密码" :visible.sync="changePassDialog" width="500px" center @close="resetForm('ruleForm')" custom-class="changePassDialog" :close-on-click-modal="false">
        <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="90px">
          <el-form-item label="旧密码" prop="oldPasswords">
            <el-input clearable type="password" v-model.trim="ruleForm.oldPasswords"  placeholder="请输入旧密码" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="新密码" prop="newPasswords">
            <el-input clearable type="password" v-model.trim="ruleForm.newPasswords"  placeholder="8-16位/至少包含数字/字母两种组合" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="确认密码" prop="checkPasswords">
            <el-input clearable type="password" v-model.trim="ruleForm.checkPasswords" placeholder="请再次确认新密码" autocomplete="off"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click.native="resetForm('ruleForm')">取 消</el-button>
          <el-button type="primary" @click="submitForm('ruleForm')">确 认</el-button>
        </div>
      </el-dialog>
    </div>
  </header>
</template>
<script>
import formRule from "../../util/formRule"
export default {
  name: 'Header',
  data () {
     var validatePass = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请再次输入密码'));
      } else if (value !== this.ruleForm.newPasswords) {
        callback(new Error('两次输入密码不一致!'));
      } else {
        callback();
      }
    };
    return {
      logoutDialog:false, //退出登录弹窗
      changePassDialog:false, //修改密码弹窗
      realName:'', //用户名
      organizationName:'', //组织机构名称
      ruleForm: {
        oldPasswords: '',
        newPasswords: '',
        checkPasswords: '',
      },
      rules: {
        oldPasswords: [
          { required: true, message: "旧密码不能为空", trigger: "blur" },
          { validator: formRule.validatePassword, trigger: 'blur' }
        ],
        newPasswords: [
          { required: true, message: "新密码不能为空", trigger: "blur" },
          { validator: formRule.validatePassword, trigger: 'blur' }
        ],
        checkPasswords: [
          { required:true,validator:validatePass, trigger: 'blur' }
        ]
      }
    }
  },
  created() {
    this.realName = localStorage.getItem("realName")
    this.organizationName = localStorage.getItem('organizationName')
  },
  methods: {
    //退出登录
    handlLogOut () {
      this.$store.dispatch('logOut').then((res) => {
        if(res.code===200){
          this.$message.success('退出登录');
        }
      })
    },
    //保存设置
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.$http.post(process.env.VUE_APP_API+'/manager/user/updatePasswords',{
            'oldPasswords':this.ruleForm.oldPasswords,
            'newPasswords':this.ruleForm.newPasswords
          },false).then(res => {
            if(res.code===200){
              this.changePassDialog = false
              this.$message.success('修改成功');
              this.$router.push({path:'/login'})
            }else{
              this.$message.error(res.msg);
            }
          })
          .catch(error => {
            this.$message.error(error);
          });
        }
      });
    },
    //取消设置
    resetForm(formName) {
      this.changePassDialog = false
      this.$refs[formName].resetFields();
    }
  }
}
</script>
<style lang="scss">
$headerHeight: 60px;
.header {
  position: relative;
  height: $headerHeight;
  padding: 15px 0px;
  display:flex;
  flex-direction:row;
  flex-wrap: nowrap;
  justify-content:space-between;
  align-items: center;
  color:#fff;
  i{
    color:#fff;
  }
  .header-left{
    display:flex;
    align-items:center;
    h1{
      font-size:26px;
      font-weight:bold;
    }
  }
  .header-right {
    color: rgba(0, 0, 0, .65);
    display:flex;
    .user-box{
      display:flex;
      align-items:center;
      .user-img{
        .img-box{
          width:40px;
          height:40px;
        }
      }
      .user-info{
        padding:0px 10px;
        color:#fff;
      }
      i{
        font-size:18px;
        font-weight:bold;
      }
    }
  }
}
.changePassDialog{
  .el-dialog__body{
    padding:25px 70px !important;
  }
}
</style>
