/*
 * @Author: xiaoshui.candy 
 * @Date: 2019-07-23 10:18:29 
 * @Last Modified by: xiaoshui.candy
 * @Last Modified time: 2019-07-23 10:39:51
 * @desc:'富文本编辑器'
 * @params:{String} [value] 当前编辑器内容
 * @emitEvent:change 编辑器内容发生改变事件
 * @example: <Qeditor v-model.trim="ruleForm.agreementContent" ref='Qeditor' @change="editorChange"></Qeditor>
 */
<template>
  <div class="quill-editor">
    <quill-editor v-model="content"
      :options="editorOption"
      @blur="onEditorBlur($event)"
      @focus="onEditorFocus($event)"
      @ready="onEditorReady($event)">
    </quill-editor>
  </div>
</template>

<script>
  import VueQuillEditor, { Quill } from 'vue-quill-editor' //引入控件
  import './fontFamily.css'
  // import { ImageDrop } from 'quill-image-drop-module' //拖拽控件
  // import ImageResize from 'quill-image-resize-module' //缩放控件
  // Quill.register('modules/imageDrop', ImageDrop) 
  // Quill.register('modules/imageResize', ImageResize)
  // Font
  const FontList = ['SansSerif', 'Serif', 'Monospace', 'Arial', '宋体', '黑体', '微软雅黑', '新宋体', '华文新魏']
  const Font = Quill.import('formats/font')
  Font.whitelist = FontList
  // Font Size
  const SizeList = ['14px', '16px', '18px', '20px', '22px', '24px', '26px']
  const Size = Quill.import('attributors/style/size')
  Size.whitelist = SizeList
  Quill.register(Font, true)
  Quill.register(Size, true)
  export default {
    name:'Qeditor',
    props: {
      value: {
        type: String,
        default: ""
      }
    },
    watch: {
      //双向绑定数据
      content: function(newval, val) {
        this.$emit("input", newval);
      },
      value: function(newval, val) {
        this.content = newval;
      }
    },
    data() {
      return {
        content: this.value, //富文本内容
        editorOption: { //富文本配置项
          placeholder:'请输入内容',
          modules: {
            toolbar: [ //工具栏
               ['bold', 'italic', 'underline', 'strike'],
               ['blockquote', 'code-block'],
               [{ 'header': 1 }, { 'header': 2 }],
               [{ 'list': 'ordered' }, { 'list': 'bullet' }],
               [{ 'script': 'sub' }, { 'script': 'super' }],
               [{ 'indent': '-1' }, { 'indent': '+1' }],
               [{ 'direction': 'rtl' }],
               [{ 'size': SizeList }],
               [{ 'header': [1, 2, 3, 4, 5, 6, false] }],
               [{ 'font': FontList }],
               [{ 'color': [] }, { 'background': [] }],
               [{ 'align': [] }],
                ['clean'],
               ['link', 'image'],
            ],
            history: {
              delay: 1000,
              maxStack: 50,
              userOnly: false
            },
            // imageDrop: true, //是否开启拖拽
            // imageResize: { //缩放配置
            //   displayStyles: {
            //     backgroundColor: 'black',
            //     border: 'none',
            //     color: 'white'
            //   },
            //   modules: [ 'Resize', 'DisplaySize', 'Toolbar' ]
            // }
          }
        }
      }
    },
    mounted() {
    },
    methods: {
      //失焦时触发
      onEditorBlur(editor) {
        this.$emit('change')
      },
      //获取焦点时触发
      onEditorFocus(editor) {
        this.$emit('change')
      },
      //editor实例化时触发
      onEditorReady(editor) {
        this.$emit('change')
      }
    }
  }
</script>

<style>
  .quill-editor:not(.bubble) .ql-container,
  .quill-editor:not(.bubble) .ql-container .ql-editor {
    height: 30rem;
    padding-bottom: 1rem;
  }
</style>

<style lang="scss" scoped>
  .quill-editor,
  .quill-code {
    width: 100%;
    float: left;
    line-height:initial;
  }
  .quill-code {
    height: auto;
    border: none;
    > .title {
      border: 1px solid #ccc;
      border-left: none;
      height: 3em;
      line-height: 3em;
      text-indent: 1rem;
      font-weight: bold;
    }
    > code {
      width: 100%;
      margin: 0;
      padding: 1rem;
      border: 1px solid #ccc;
      border-top: none;
      border-left: none;
      border-radius: 0;
      height: 30rem;
      overflow-y: auto;
    }
  }
</style>